@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: whitesmoke;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.container{
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.card {
    background-color: whitesmoke;
    width: 60%;
    min-width: 340px;
    border-radius: 5px;
    padding: 10px 20px;
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

a{
    width: 100%;
    text-align: center;
}

img{
    cursor: pointer;
    border-radius: 5px;
    width: auto;
    max-height: 80vh;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}

h5 {
    margin: 0;
}

p {
    text-align: justify;
    margin: 20px;
}

/* Large Smartphone (Vertical) */
@media screen and (max-width: 800px) {
    .card {
        width: 100%;
        max-height: -webkit-fit-content;
        max-height: -moz-fit-content;
        max-height: fit-content;
        border-radius: 0;
        padding-left: 10px;
    }

    h5, h3 {
        margin-left: 20px;
    }

    img{
        width: 90%;
    }
}

.btnContainer {
  width: 90%;
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

button {
  background: rgba(236, 198, 116, 0.2);
  width: 250px;
  height: 50px;
  padding: 0 20px;
  text-align: center;
  border-style: none;
  outline: none;
  border-bottom: 1px solid black;
  cursor: pointer;
  font-family: 'Open Sans', sans-serif;
  border-radius: 5px 5px 0 0;
  transition: 0.25s;
}

.slide:hover {
  box-shadow: inset 0 -50px 0 0 black;
  color: rgb(236, 198, 116);;
}

/* Large Smartphone (Vertical) */
@media screen and (max-width: 800px) {
  .btnContainer {
    justify-content: center;
    width: 100%;
  }

  .slide{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
  }

  .slide:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
    color: black;
  }
}

.main-container {
  width: 100vw;
  min-height: 100vh;
  background: rgba(236, 198, 116, 0.2);
  font-family: 'Open Sans', sans-serif;
}

.title{
  width: 100%;
  height: 15vh;
  background-color: rgb(236, 198, 116);
  display: flex;
  justify-content: center;
  align-items: center;
  /* font-weight: 700; */
}

/* Loader */
.loader {
  width: 100vw;
  height: 100vh;
  background: rgba(236, 198, 116, 0.2);
  position: fixed;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 800px){
  .title{
      font-size: 0.8rem;
  }
}
